export default {
  layout: 'none',
  components: {
  },
  asyncData({ params, $axios }) {
  },
  data() {
    return {
      rules: {
        required: value => !!value.trim() || '必填，不可为空',
        mail: value => /^\S+@\S+\.\S+?$/.test(value) || '请输入正确的邮箱地址',
        password: (value => value.length > 5 && value.length < 17) || '密码长度需为6~16位',
        confirm: value => value === this.form.userPass || '两次输入密码不一样'
      },
      confirmPassword: '',
      form: {
        email: '',
        vertifyCode: '',
        type: 0,
        userPass: ''
      },
      redirect: undefined,
      otherQuery: {},
      loading: false,
      msg: '',
      snackbar: false,
      vertifyButtonText: '获取验证码',
      vLoading: false,
      counter: 0
    }
  },
  computed: {},
  watch: {
    $route: {
      handler(route) {
        const query = route.query
        if (query) {
          this.redirect = query.redirect
          this.otherQuery = this.getOtherQuery(query)
        }
      },
      immediate: true
    }
  },
  created() {
  },
  mounted() {
  },
  methods: {
    getOtherQuery(query) {
      return Object.keys(query).reduce((acc, cur) => {
        if (cur !== 'redirect') {
          acc[cur] = query[cur]
        }
        return acc
      }, {})
    },
    changePassword() {
      if (!this.$refs.form.validate()) {
        return
      }
      this.loading = true
      const form = this.form
      form.type = 1
      this.$axios.post('/findPass', form).then((data) => {
        this.loading = false
        this.$router.push({ path: this.redirect || '/login', query: this.otherQuery })
      }).catch((err) => {
        console.log(err)
        this.loading = false
        this.msg = err.response.data.resMsg
        this.snackbar = true
      })
    },
    sendVertifyCode() {
      if (this.counter > 0 || this.rules.mail(this.form.email) !== true) {
        return
      }
      this.vLoading = true
      const form = this.form
      form.type = 0
      this.$axios.post('/findPass', form).then((data) => {
        this.vLoading = false
        this.timer()
        this.msg = '验证码发送成功，请在前往邮箱查收'
        this.snackbar = true
      }).catch((err) => {
        console.log(err)
        this.vLoading = false
        this.msg = err.response.data.resMsg
        this.snackbar = true
      })
    },
    timer() {
      if (this.counter > 0) {
        return
      }
      this.counter = 60
      this.vertifyButtonText = `重新发送（${this.counter}s）`
      const interval = setInterval(() => {
        // console.log(this.counter, this.vertifyButtonText)
        if (--this.counter > 0) {
          this.vertifyButtonText = `重新发送（${this.counter}s）`
        } else {
          clearInterval(interval)
          this.vertifyButtonText = `重新发送`
        }
      }, 1000)
    }
  }
}
